.month{
  position: relative;
  min-width: 75px;
  &:not(:last-child){
    margin-bottom: 20px;
  }
  &Title{
    cursor: pointer;
  }
  &List{
    position: absolute;
    right: -10px;
    top: 0;
    transform: translateX(100%);
    list-style: none;
    border: 1px solid #282c34;
    padding: 10px;
    width: 200px;
    text-align: center;
    &Item{
      &:not(:last-child){
        margin-bottom: 5px;
      }
    }
  }
}